.card {
  align-items: flex-end;
  background-image: url(/static/img/rectangle-5-1.svg);
  background-size: 100% 100%;
  display: flex;
  height: 481px;
  justify-content: flex-end;
  min-width: 360px;
  padding: 30px 29px;
}

.frame-5 {
  align-items: flex-start;
  background-color: var(--white);
  border-radius: 24.5px;
  display: flex;
  height: 49px;
  justify-content: flex-end;
  min-width: 49px;
  padding: 16.8px 15.8px;
}

.vector-5 {
  height: 15px;
  width: 17px;
}

.card.card-1 {
  background-image: url(/static/img/rectangle-5-2.svg);
}

.card.card-2 {
  background-image: url(/static/img/rectangle-5.svg);
}

